<template>
  <el-container class="home-container">
    <!-- 头部区域 -->
    <el-header>
      <div>
        <img src="../assets/title.jpg" alt="">
        <div class="text item">数据统计{{this.list.times}}</div>
      </div>
    </el-header>
    <!-- 页面主体区域 -->
    <el-container>
        <!-- 侧边栏 -->
        <el-aside>
          <!-- 侧边栏菜单区域 -->
          <el-menu background-color="#2555B7" text-color="#fff"
          active-text-color="#2C8AFF"  :unique-opened="true"
          router :default-active="activePath">
            <el-menu-item index="/data" @click="saveNavState('/data')">
            <i class="el-icon-menu"></i>
            <span slot="title">实时数据</span>
            </el-menu-item>
            <el-menu-item index="/map" @click="saveNavState('/map')">
            <i class="el-icon-map-location"></i>
            <span slot="title">确诊人数地图</span>
            </el-menu-item>
            <el-menu-item index="/history" @click="saveNavState('/history')">
            <i class="el-icon-data-line"></i>
            <span slot="title">历史数据</span>
            </el-menu-item>
            <el-menu-item index="/world" @click="saveNavState('/world')">
            <i class="el-icon-map-location"></i>
            <span slot="title">全球确诊分布</span>
            </el-menu-item>
            <el-menu-item index="/tips" @click="saveNavState('/tips')">
            <i class="el-icon-info"></i>
            <span slot="title">预防须知</span>
            </el-menu-item>
          </el-menu>
        </el-aside>
        <!-- 右侧内容主体 -->
        <el-main>
            <!-- 路由占位符 -->
            <router-view></router-view>
        </el-main>
    </el-container>
  </el-container>
</template>

<script>
import jsonp from 'jsonp'
export default {
  name:'Home',
  data() {
      return{
        list:{},
        activePath:''
      }
  },
  created() {
    this.activePath = window.sessionStorage.getItem('activePath')
    // console.log(this.activePath);
  },
  mounted(){
    this.getData()
  },
  methods:{
      // 保存链接的激活状态
    saveNavState(activePath) {
      window.sessionStorage.setItem('activePath', activePath)
      this.activePath = activePath
    },
    getData() {
		// jsonp 跨域请求
		jsonp(
			'http://interface.sina.cn/news/wap/fymap2020_data.d.json?_=1580892522427', {}, (erros, data) => {
        this.list = data.data
			})
    }
  }
}
</script>

<style scoped>
  .home-container{
    height: 100%;
  }
  .el-header{
    position: relative;
    height: 128px !important;
    display: flex;
    /* justify-content: space-between; */
    padding-left: 0;
    align-items: center;
    font-size:14px;
  }
  .el-header img{
    display: flex;
    align-items: center;
    width: 39.9%;
  }
  .el-main{
      padding: 0;
  }
  .text{
      position: absolute;
      top: 40px;
      left: 340px;
  }
</style>

