import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '@/components/Home.vue'

const Data = () => import(/* webpackChunkName:"login_home_welcome" */ '../components/Data.vue')
const Map = () => import(/* webpackChunkName: "about" */ '../components/Map.vue')
const History = () => import(/* webpackChunkName: "about" */ '../components/History.vue')
const World = () => import(/* webpackChunkName: "about" */ '../components/World.vue')
const Tips = () => import(/* webpackChunkName: "about" */ '../components/Tips.vue')

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    redirect: '/data',
    children: [
      { path: '/data', component: Data },
      { path: '/map', component: Map },
      { path: '/history', component: History },
      { path: '/world', component: World },
      { path: '/Tips', component: Tips }
    ] 
  }
]

const router = new VueRouter({
  routes
})

export default router
